 .footer {
  position: flex;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #000000;
  background-image: linear-gradient(147deg, #000000 0%, #04619f 74%);
}

.Box {
  border-top: 0.1px solid #4e4e4e;
  border-bottom: 0.1px solid #4e4e4e;
  
  width: 100%;

  @media (max-width: 500px) {
    padding: 70px 30px;
  }
}


